.customers {
  width: $mobile;
  margin: 0 auto;
  text-align: center;
  background-color: #11131f;

  @media screen and(min-width: $tablet) {
    width: 768px;
  }
}

.customers__title {
  color: $main-color;
  font-family: DM Sans;
  font-size: 32px;
  font-weight: 400;
  line-height: 40px;
  letter-spacing: -0.045em;
  text-align: left;

  @media screen and(min-width: $tablet) {
    width: 480px;
    text-align: start;
    margin-bottom: 32px;
  }

  &--color {
    color: $focus-state;
    font-family: DM Sans;
    font-size: 32px;
    font-style: italic;
    font-weight: 500;
    line-height: 28.8px;
    letter-spacing: -4, 5%;

    text-align: left;
  }
}

.customers__subtitle {
  position: relative;
  margin-top: 24px;
  color: $main-color;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 19.6px;
  letter-spacing: -0.02em;
  text-align: left;

  @media screen and(min-width: $tablet) {
    width: 242px;
    margin-left: 461px;
    margin-bottom: 64px;
  }
}

.rewiew-list {
  &__item {
    width: 335px;
    display: flex !important;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
    background-color: $grey;
    // width: 335px;
    // height: 208px;
    // left: 20px;
    padding: 24px 24px 24px 24px;
    gap: 8px;
    border-radius: 24px;
    @media screen and(min-width: $tablet) {
    }
  }
  &__name {
    margin-top: 18px;
    color: $main-color;
    font-family: DM Sans;
    font-size: 16px;
    font-weight: 500;
    line-height: 17.6px;
    letter-spacing: -0.045em;
    text-align: center;
  }
  &__comment {
    text-align: start;
    margin-top: 8px;
    color: rgba(246, 245, 239, 0.5);
    font-family: DM Sans;
    font-size: 12px;
    font-weight: 400;
    line-height: 16.8px;
    letter-spacing: -0.02em;
    text-align: Center;
    @media screen and(min-width: $tablet) {
      width: 242px;
    }
  }
  &__image {
    width: 48px;
    height: 48px;
  }
}

.slick-dots {
  margin-top: 31px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.slick-dots button {
  font-size: 0;
  position: relative;
  // opacity: 0;
  width: 13px;
  height: 13px;
  border-radius: 50px;
}

.slick-dots button::before {
  content: '';
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50px;
  background-color: rgba(31, 33, 45, 1);
}

.slick-active button::before {
  background-color: rgba(136, 179, 185, 1);
  scale: 1.2;
}

.slick-initialized .slick-slide {
}
